// components/MainApp.js
import React, { useState, useEffect } from "react";
import { useNavigate, Link } from "react-router-dom";
import { ref, uploadBytes, getDownloadURL } from "firebase/storage";
import { storage } from "../firebase";
import axios from "axios";
import RecipeDisplay from "./RecipeDisplay";
import { useCharacter } from "../contexts/CharacterContext";


const NAME_MAP = {
  baek:  "AI",
  seung: "3AI",
  jang:  "",
};

// id    
const IMG_MAP = {
  baek:  "/images/baek.png",
  seung: "/images/seung.png",
  jang:  "/images/jang.png",
};

const BACKEND_API_URL = "https://jang-geum-i-backend.onrender.com";

function MainApp({ user, setUser }) {
  const [inputText, setInputText] = useState("");
  const [imageUrl, setImageUrl] = useState(null);
  const [result, setResult] = useState(null);
  const [status, setStatus] = useState("idle");
  const [error, setError] = useState(null);
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const [searchHistory, setSearchHistory] = useState([]);
  const [searchResult, setSearchResult] = useState(null);
  const navigate = useNavigate();
  const { character } = useCharacter();
  const displayName = NAME_MAP[character] || "";
  const avatarSrc = IMG_MAP[character] || "";

  const MAX_FILE_SIZE = 5 * 1024 * 1024;
  const SUPPORTED_FILE_TYPES = ["image/jpeg", "image/png", "image/gif"];

  const uploadFileToFirebase = async (file) => {
    const storageRef = ref(storage, `images/${file.name}`);
    await uploadBytes(storageRef, file);
    return await getDownloadURL(storageRef);
  };

  const fetchRecipeFromBackend = async (payload) => {
    setStatus("extracting");
    const response = await fetch(`${BACKEND_API_URL}/upload`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(payload),
      credentials: "include",
    });
    if (!response.ok) {
      const responseText = await response.text();
      throw new Error(`HTTP Error ${response.status}: ${responseText}`);
    }
    return await response.json();
  };

  useEffect(() => {
    setUser(localStorage.getItem("username") || null);
  }, []);

  const handleUpload = async (file) => {
    setError(null);
    setResult(null);
    setSearchResult(null);
    setImageUrl(null);
    setStatus("processing");

    try {
      let payload;
      if (inputText.trim()) payload = { query: inputText };

      if (file) {
        if (file.size > MAX_FILE_SIZE) throw new Error("  5MB   .");
        if (!SUPPORTED_FILE_TYPES.includes(file.type)) throw new Error("   .");
        const uploadedImageUrl = await uploadFileToFirebase(file);
        setImageUrl(uploadedImageUrl);
        payload = { imageUrl: uploadedImageUrl };
      }

      if (!payload) throw new Error("   .");

      const data = await fetchRecipeFromBackend(payload);
      setResult(data);
      setStatus("complete");
    } catch (err) {
      setError(` : ${err.message || "   "}`);
      setStatus("idle");
    }
  };

  //   
  const handleDrop = (event) => {
    event.preventDefault();
    const files = event.dataTransfer.files;
    if (files && files[0]) {
      handleUpload(files[0]);
    }
  };

  const handleDragOver = (event) => {
    event.preventDefault();
  };

  // " " 
  const handleStartCooking = () => {
    if (!result || !result.dish) return alert(" !   .");
    navigate("/cooking", { state: { recipe: result } });
  };

  const handleNavigateToPurchase = () => {
    if (!result) return alert("  .");
    navigate("/purchase", { state: { recipe: result } });
  };

  // 
  const handleLogout = async () => {
    try {
      await axios.post(`${BACKEND_API_URL}/api/users/logout`, {}, { withCredentials: true });
      localStorage.removeItem("username");
      setUser?.(null);
      setIsSidebarOpen(false);
      alert("  .");
      navigate("/");
    } catch (error) {
      console.error("  :", error);
    }
  };

  const fetchNewRecipe = async () => {
    if (!inputText.trim()) return alert(" !");
    setError(null);
    setResult(null);
    setSearchResult(null);
    setImageUrl(null);
    setStatus("extracting");
    setInputText("");

    try {
      const response = await axios.post(`${BACKEND_API_URL}/upload`, { query: inputText }, { withCredentials: true });
      setSearchResult(response.data);
      setStatus("complete");
    } catch (error) {
      setStatus("idle");
    }
  };

  const fetchSearchHistory = async () => {
    try {
      const response = await axios.get(`${BACKEND_API_URL}/api/recipes/search-history`, { withCredentials: true });
      if (response.data?.history) setSearchHistory(response.data.history);
    } catch (error) {
      console.error("    :", error);
    }
  };

   //       
   const handleShowSearchHistory = () => {
    fetchSearchHistory();
  };

  const handleRecipeClick = (recipe) => setSearchResult(recipe);

  //    
  const styles = {
    authButton: {
      padding: "8px 15px",
      fontSize: "0.9rem",
      backgroundColor: "#4CAF50",
      color: "white",
      borderRadius: "5px",
      textDecoration: "none",
      cursor: "pointer",
    },
  };


  return (
    <div
      style={{
        fontFamily: "Arial, sans-serif",
        backgroundColor: "#f9f9f9",
        color: "#333",
        minHeight: "100vh",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
  
        /*   gap   */
        gap: "8px",  //   16px 
  
        padding: "20px",
      }}
    >
      {/* 1. () */}
      {avatarSrc && (
        <img
          src={avatarSrc}
          alt={displayName}
          style={{
            width: "250px",
            height: "250px",
            borderRadius: "20px",
            margin: 0,  //   
          }}
        />
      )}
  
      {/* 2. () */}
      <h1
        style={{
          fontSize: "3rem",
          color: "#4CAF50",
          margin: 0,  //  h1  
        }}
      >
        {displayName}
      </h1>
  
      {/* 3.  */}
      <div
        style={{
          width: "100%",
          maxWidth: "400px",
          padding: "15px",
          backgroundColor: "#fff",
          borderRadius: "10px",
          textAlign: "center",
          boxShadow: "0 4px 8px rgba(0,0,0,0.1)",
          border: "1px solid #ddd",
          margin: 0,  //   
        }}
      >
        <input
          type="text"
          value={inputText}
          onChange={(e) => setInputText(e.target.value)}
          placeholder=" !"
          style={{
            width: "80%",
            maxWidth: "250px",
            fontSize: "0.9rem",
            border: "1px solid #ccc",
            borderRadius: "5px",
            marginBottom: "15px",
            outline: "none",
          }}
        />
        <button
          onClick={fetchNewRecipe}
          style={{
            padding: "10px 15px",
            fontSize: "0.9rem",
            backgroundColor: "#4CAF50",
            color: "#fff",
            border: "none",
            borderRadius: "5px",
            cursor: "pointer",
            transition: "background-color 0.3s ease",
          }}
          onMouseEnter={(e) => (e.target.style.backgroundColor = "#45a049")}
          onMouseLeave={(e) => (e.target.style.backgroundColor = "#4CAF50")}
        >
          
        </button>
      </div>

      <div
        onDrop={handleDrop}
        onDragOver={handleDragOver}
        style={{
          width: "100%",
          maxWidth: "500px",
          padding: "30px",
          backgroundColor: "#fff",
          borderRadius: "10px",
          textAlign: "center",
          boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)",
          border: "2px dashed #ccc",
        }}
      >
        <p style={{ color: "#666", marginBottom: "20px", fontSize: "1rem" }}>
             <br />
          <label
            htmlFor="fileInput"
            style={{
              color: "#4CAF50",
              textDecoration: "underline",
              cursor: "pointer",
            }}
          >
             
          </label>
          .
        </p>
        <input
          type="file"
          id="fileInput"
          style={{ display: "none" }}
          onChange={(e) => handleUpload(e.target.files[0])}
        />
        {status === "uploading" && (
          <p style={{ color: "blue" }}>  ...</p>
        )}
        {status === "extracting" && (
          <p style={{ color: "blue" }}>  ...</p>
        )}
        {error && <p style={{ color: "red" }}>{error}</p>}
        {imageUrl && (
          <img
            src={imageUrl}
            alt="Uploaded"
            style={{
              marginTop: "20px",
              maxWidth: "100%",
              height: "auto",
              borderRadius: "10px",
              border: "1px solid #ccc",
            }}
          />
        )}
      </div>

      {/*       */}
      <div style={{ position: "absolute", top: "10px", right: "20px" }}>
        {user ? (
          <>
            <span
            style={{ fontWeight: "bold", color: "#4CAF50", cursor: "pointer" }}
            onClick={() => setIsSidebarOpen(!isSidebarOpen)} //    
          >
            {user}
          </span>
            <button onClick={handleLogout} style={styles.logoutButton}></button>
          </>
        ) : (
          <div>
            <Link to="/login" style={styles.authButton}></Link>
            <Link to="/register" style={styles.authButton}></Link>
          </div>
        )}
      </div>

      {/*   ( ) */}
      <div
        style={{
          position: "fixed",
          top: 0,
          right: isSidebarOpen ? "0px" : "-300px", //    0px,   -300px
          width: "250px",
          height: "100vh",
          backgroundColor: "#fff",
          boxShadow: "0 4px 8px rgba(0,0,0,0.2)",
          transition: "right 0.3s ease-in-out",
          padding: "20px",
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        {/*     */}
        <button
          onClick={() => setIsSidebarOpen(false)}
          style={{
            position: "absolute",
            top: "10px",
            left: "15px",
            fontSize: "1.5rem",
            background: "none",
            border: "none",
            cursor: "pointer",
            color: "#888",
          }}
        >
          
        </button>

        <h2 style={{ color: "#4CAF50" }}>  </h2>
        <p style={{ fontSize: "1.1rem", fontWeight: "bold" }}>{user}</p>

        <button
          onClick={handleShowSearchHistory}
          style={{
            marginTop: "20px",
            padding: "10px",
            backgroundColor: "#007BFF",
            color: "white",
            border: "none",
            borderRadius: "5px",
            cursor: "pointer",
          }}
        >
             
        </button>

        {/*       ( ) */}
        <div
          style={{
            flex: 1, 
            overflowY: "auto",  //    
            overflowX: "hidden",
            maxHeight: "60vh",  //     70%  
            width: "100%", 
            paddingRight: "5px", //        
            marginTop: "10px",
          }}
        >
          <ul style={{ width: "100%", padding: "10px", listStyle: "none" }}>
            {searchHistory.length > 0 ? (
              searchHistory.map((entry, index) => (
                <li
                  key={index}
                  style={{
                    padding: "10px",
                    borderBottom: "1px solid #ddd",
                    cursor: "pointer",
                    color: "#333",
                    whiteSpace: "nowrap", //    
                    overflow: "hidden",
                    textOverflow: "ellipsis", //    ... 
                  }}
                  onClick={() => handleRecipeClick(entry.recipe)}
                >
                  {entry.query} - {entry.recipe.dish}
                </li>
              ))
            ) : (
              <p style={{ color: "#666", marginTop: "10px" }}>   .</p>
            )}
          </ul>
        </div>

        <button
          onClick={handleLogout}
          style={{
            marginTop: "20px",
            padding: "10px 15px",
            backgroundColor: "red",
            color: "white",
            border: "none",
            borderRadius: "5px",
            cursor: "pointer",
          }}
        >
          
        </button>
      </div>

      {searchResult && (
        <RecipeDisplay recipe={searchResult} navigate={navigate} />
      )}
    
      {status === "complete" && result && (
        <div
          style={{
            marginTop: "30px",
            textAlign: "left",
            backgroundColor: "#fff",
            padding: "20px",
            borderRadius: "10px",
            color: "#333",
            boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)",
            width: "100%",
            maxWidth: "800px",
          }}
        >
          <h2
            style={{ color: "#4CAF50", fontSize: "1.5rem", marginBottom: "20px" }}
          >
             : {result.dish}
          </h2>

          <div>
            <h3 style={{ fontSize: "1.2rem", marginBottom: "10px" }}>:</h3>
            {result.ingredients && result.ingredients.length > 0 ? (
              <ul>
                {result.ingredients.map((ingredient, index) => (
                  <li key={index} style={{ marginBottom: "5px" }}>
                    {ingredient.name}: {ingredient.quantity}
                  </li>
                ))}
              </ul>
            ) : (
              <p style={{ color: "#666" }}>  .</p>
            )}
          </div>

          <div>
            <h3 style={{ fontSize: "1.2rem", marginBottom: "10px" }}>
              :
            </h3>
            {result.instructions ? (
              Array.isArray(result.instructions) ? (
                <ul style={{ paddingLeft: "20px", listStyleType: "decimal" }}>
                  {result.instructions.map((step, index) => (
                    <li key={index} style={{ marginBottom: "5px" }}>
                      {step}
                    </li>
                  ))}
                </ul>
              ) : (
                <pre
                  style={{
                    backgroundColor: "#f9f9f9",
                    padding: "10px",
                    borderRadius: "10px",
                    border: "1px solid #ccc",
                  }}
                >
                  {result.instructions}
                </pre>
              )
            ) : (
              <p style={{ color: "#666" }}>  .</p>
            )}
          </div>

          <div style={{ display: "flex", justifyContent: "center", gap: "10px", marginTop: "20px" }}>
            <button
              onClick={handleNavigateToPurchase}
              style={{
                padding: "10px 15px",
                fontSize: "1rem",
                backgroundColor: "#ccc",
                color: "#333",
                border: "none",
                borderRadius: "5px",
                cursor: "pointer",
              }}
            >
              
            </button>
            <button
              onClick={handleStartCooking}
              style={{
                padding: "10px 15px",
                fontSize: "1rem",
                backgroundColor: "#4CAF50",
                color: "#fff",
                border: "none",
                borderRadius: "5px",
                cursor: "pointer",
              }}
            >
               
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

export default MainApp;
